.aboutpage-container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 30px;
}
.aboutpage-container::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: brightness(50%);
  background-image: url(data:image/png;base64,);
}

.aboutpage-category {
  position: absolute;
  display: flex;
  top: 100px;
  width: 300px;
  align-items: center;
  justify-content: space-between;
}

.aboutpage-category li {
  font-weight: 200;
}

.aboutpage-box {
  position: absolute;
  left: 0%;
  top: 0%;
  width: 300%;
  height: 100%;
  display: flex;
  transition: 0.5s;
}
