.mainpage-container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 30px;
}

.mainpage-container::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: brightness(50%);
  background-image: url(data:image/png;base64,);
}

.mainpage-flower {
  position: absolute;
  width: 500px;
  height: 500px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.mainpage-flower img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  filter: brightness(50%);
  transition: 1s;
}

.mainpage-titleBox {
  position: relative;
  width: 800px;
  height: 150px;
}
.mainpage-titleBox ul {
  width: 100%;
  height: 60%;
  font-size: 50px;
  font-weight: 100;
  letter-spacing: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}
.mainpage-titleBox ul li {
  transition: 0.5s;
}
.mainpage-titleBox div {
  font-size: 12px;
  display: flex;
  justify-content: center;
  transition: 1s;
}

.mainpage-zeroone-box {
  position: absolute;
  width: 25%;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mainpage-zeroone-box div:nth-child(1),
.mainpage-zeroone-box div:nth-child(3) {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 15px;
  font-size: 12px;
  font-weight: 700;
}

.mainpage-zeroone-box div:nth-child(2) {
  width: 40%;
  height: 2px;
  background: linear-gradient(to right, #363636 50%, #af7340 50%);
}

.mainpage-zeroone-box div:nth-child(1) {
  position: relative;
  color: #363636;
}

.mainpage-zeroone-box div:nth-child(3) {
  color: #af7340;
}

@media (max-width: 800px) {
  .mainpage-flower img {
    filter: brightness(40%);
  }
  .mainpage-titleBox ul {
    font-size: 30px;
    letter-spacing: 20px;
  }

  .mainpage-titleBox ul li:nth-child(1) {
    transition-delay: 0.8s;
  }
  .mainpage-titleBox ul li:nth-child(2) {
    transition-delay: 0.5s;
  }
  .mainpage-titleBox ul li:nth-child(3) {
    transition-delay: 0.7s;
  }
  .mainpage-titleBox ul li:nth-child(4) {
    transition-delay: 0.5s;
  }
  .mainpage-titleBox ul li:nth-child(5) {
    transition-delay: 0.3s;
  }
  .mainpage-titleBox ul li:nth-child(6) {
    transition-delay: 0.6s;
  }
  .mainpage-titleBox ul li:nth-child(7) {
    transition-delay: 0.4s;
  }
  .mainpage-titleBox ul li:nth-child(8) {
    transition-delay: 0.8s;
  }
  .mainpage-titleBox ul li:nth-child(9) {
    transition-delay: 0.1s;
  }
  .mainpage-titleBox ul li:nth-child(10) {
    transition-delay: 1s;
  }

  .mainpage-titleBox div {
    font-size: 12px;
  }
}
