.footer-container {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 200px;
  z-index: 1;
  /* border: 1px solid red; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-container::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: brightness(50%);
  background-image: url(data:image/png;base64,);
}

.footer-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  height: 120px;
  background-color: #161616;
  box-shadow: 0px 20px 30px 5px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-wrapper-list {
  height: 100%;
}

.footer-wrapper-list:nth-child(1) {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: end;
}
.footer-wrapper-list:nth-child(1) h2 {
  position: relative;
  color: rgb(235, 235, 235);
  font-weight: 500;
  font-size: 20px;
  border-right: 1px solid rgb(255, 255, 255, 0.2);
  width: 200px;
  left: 20px;
  height: 60px;
  padding-right: 30px;
  display: flex;
  align-items: center;
  justify-content: end;
}
.footer-wrapper-list:nth-child(2) {
  width: 40%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer-categoryBox {
  position: relative;
  width: 80%;
  height: 40%;
  left: 5px;
  display: flex;
  align-items: end;
  margin-bottom: 2px;
}
.footer-categoryBox button {
  border: none;
  background-color: transparent;
  color: rgb(255, 255, 255, 0.3);
  margin-right: 10px;
  font-weight: 200;
  font-size: 10px;
  transition: 0.5s;
}
.footer-categoryBox button:hover {
  color: rgb(255, 255, 255, 0.7);
}
.footer-copyBox {
  position: relative;
  width: 80%;
  height: 40%;

  left: 5px;
}
.footer-copyBox p {
  color: rgb(255, 255, 255, 0.3);

  font-weight: 200;
  font-size: 10px;
}

.footer-wrapper-list:nth-child(3) {
  width: 40%;
  display: flex;
  align-items: center;
}

.footer-textBox {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 10px;
  font-weight: 200;
  color: rgb(255, 255, 255, 0.3);
  letter-spacing: 0.5px;
}
