.profileComponet {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profileComponet-card {
  width: 500px;
  height: 300px;
  transform-style: preserve-3d;
}

.profileComponet-iam,
.profileComponet-profileBox {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: 1s ease-in-out;
  backface-visibility: hidden;
  transform: perspective(1000px) rotateX(0deg);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
}

.profileComponet-card .profileComponet-iam {
  background-color: #b06019;
}

.profileComponet-iam-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  filter: brightness(50%);
  background-image: url(data:image/png;base64,);
}

.profileComponet-iam-title {
  position: absolute;
  width: 100%;
  text-align: center;
  font-weight: 500;
  /* color: rgb(46, 46, 46); */
  color: transparent;
  background-color: #181818;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  text-shadow: rgba(175, 96, 25, 0.5) 0px 3px 3px;
}

.profileComponet-card.profile .profileComponet-iam {
  transform: perspective(1000px) rotateX(180deg);
}

.profileComponet-card .profileComponet-profileBox {
  color: #b06019;
  background-color: rgb(21, 21, 21);
  transform: perspective(1000px) rotateX(180deg);
  display: flex;
  align-items: center;
}

.profileComponet-card.profile .profileComponet-profileBox {
  transform: perspective(1000px) rotateX(360deg);
}

.profileComponet-profileBox-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: brightness(100%);
  background-image: url(data:image/png;base64,);
}

.profileComponet-profileBox-img {
  width: 200px;
  height: 250px;
  position: absolute;
  left: 30px;
  top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profileComponet-profileBox-img img {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 250px;
}
.profileComponet-profileBox-img p {
  position: absolute;
  left: 0;
  top: -20px;
  font-weight: 200;
  font-size: 10px;
  color: aliceblue;
}

.profileComponet-profileBox-img div {
  position: absolute;
  width: 40px;
  height: 0.1px;
  left: 0;
  top: 30px;
}

.profileComponet-profileBox-profile {
  position: absolute;
  right: 0;
  top: 0;
  width: 250px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profileComponet-profileBox-profile ul {
  position: absolute;
  top: 30px;
  width: 200px;
  height: 250px;

  display: flex;
  justify-content: center;
  flex-direction: column;
}
.profileComponet-profileBox-profile ul li:nth-child(1) {
  position: absolute;
  display: flex;
  align-items: end;
  top: 20px;
}

.profileComponet-profileBox-profile ul li:nth-child(1) p:nth-child(2) {
  position: relative;
  font-size: 7px;
  color: rgb(231, 231, 231);
  bottom: 3px;
  margin-left: 5px;
}

.profileComponet-profileBox-profile ul li:nth-child(2) {
  position: absolute;
  top: 45px;
  left: 2px;
  font-size: 9px;
  color: rgb(188, 188, 188);
}
.profileComponet-profileBox-profile ul li:nth-child(3) {
  position: absolute;
  bottom: 20px;
  color: rgb(231, 231, 231);
  font-weight: 200;
  font-size: 9px;
}

.profileComponet-profileBox-profile ul li:nth-child(4) {
  position: absolute;
  bottom: 0;
  font-size: 7px;
  color: rgb(231, 231, 231);
  font-weight: 200;
}

.profileComponet-profileBox-profile ul li:nth-child(5) {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 7px;
  color: rgb(231, 231, 231);
  text-align: right;
  font-weight: 200;
}
