.projectpage-container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 30px;
  z-index: 1;
}
.projectpage-container::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: brightness(50%);
  background-image: url(data:image/png;base64,);
}
.projectpage-title {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 200;
  color: rgb(184, 184, 184);
}

.projectpage-title::before {
  width: 50%;
  position: absolute;
  content: "";
  left: 50%;
  height: 1px;
  top: 30px;
  background-color: rgb(184, 184, 184);
  transform: translateX(-50%);
}
.projectpage-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 1s ease-in-out;
  display: flex;
  overflow: hidden;
}
.projectpage-box {
  position: absolute;
  top: 0;
  width: 100%;
  height: 300%;
  transition: 1s top;
}
.projectpage-project {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 1s opacity;
}

.projectpage-buttonBox {
  position: absolute;
  width: 80px;
  height: 50px;
  bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.projectpage-button {
  width: 15px;
  height: 15px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transition: 0.2s;
}
.projectpage-button.active {
  border: 8px solid rgba(255, 255, 255, 1);
  transition: 0.5s;
}

.projectpage-projectNameBox {
  position: absolute;
  width: 250px;
  height: 25px;
  bottom: 70px;

  pointer-events: none;
}

.projectpage-projectNameBox .projectpage-projectNameBox-ul {
  position: relative;
  width: 100%;
  height: 90px;

  left: 0;
  transition: 1s;
}

.projectpage-projectNameBox .projectpage-projectNameBox-list {
  position: relative;
  width: 250px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
  color: rgba(255, 255, 255, 0.7);
}

.projectpage-projectNameBox .projectpage-projectNameBox-list span {
  position: relative;
  left: 5px;
  color: #c76a18;
}
